/* === HEADER FIJO === */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #0b2447;
  color: #ffffff;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  z-index: 1000;
}

.header h1 {
  margin: 0;
  font-size: 1.5rem;
  letter-spacing: 1px;
}

.header button {
  background-color: #1d4ed8;
  color: white;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
  font-weight: 500;
}

.header button:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
}

/* === CONTENEDOR PRINCIPAL === */
.container {
  max-width: 900px;
  margin: 8rem auto 3rem;
  padding: 2rem;
  background: #f9fafc;
  border-radius: 16px;
  font-family: "Montserrat", sans-serif;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
}

/* === LISTA DE DEBATES === */
.debateList {
  list-style: none;
  padding: 0;
  margin: 2rem 0;
}

.debateItem {
  background-color: #ffffff;
  border-radius: 12px;
  padding: 1.2rem 1.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  border: 1px solid #e2e8f0;
  transition: all 0.2s ease;
}

.debateItem:hover {
  transform: translateY(-2px);
  border-color: #1e3a8a;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
}

.debateTitle {
  font-size: 1.3rem;
  color: #0b2447;
  margin-bottom: 0.3rem;
}

.debateDescription {
  color: #475569;
  margin-bottom: 0.5rem;
}

.debateAuthor {
  font-size: 0.85rem;
  color: #64748b;
}

/* === BOTÓN NUEVO DEBATE === */
.newDebateButton {
  display: inline-block;
  background-color: #0b2447;
  color: white;
  padding: 0.8rem 1.4rem;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: background 0.2s ease, transform 0.2s ease;
}

.newDebateButton:hover {
  background-color: #102d5c;
  transform: translateY(-2px);
}

/* === BOTÓN VOLVER === */
.backButton {
  background-color: transparent;
  border: none;
  color: #1d4ed8;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 600;
  margin-bottom: 1rem;
  transition: color 0.2s ease;
}

.backButton:hover {
  color: #2563eb;
}

/* === FORMULARIO NUEVO DEBATE === */
input,
textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #cbd5e1;
  border-radius: 10px;
  font-size: 1rem;
  margin-bottom: 1rem;
  font-family: "Montserrat", sans-serif;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

input:focus,
textarea:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.2);
  outline: none;
}

textarea {
  resize: none;
  min-height: 100px;
}

/* === SECCIÓN DE COMENTARIOS === */
.comentariosList {
  margin-top: 1.5rem;
}

.comentario {
  background-color: #ffffff;
  border-radius: 12px;
  border: 1px solid #e2e8f0;
  padding: 1rem 1.2rem;
  margin-bottom: 1rem;
  transition: box-shadow 0.2s ease;
}

.comentario:hover {
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.07);
}

.comentario strong {
  color: #0b2447;
  font-weight: 600;
}

.comentario p {
  margin: 0.3rem 0 0;
  color: #334155;
}

/* === TEXTAREA Y BOTONES DE COMENTARIO === */
.textareaComentario {
  width: 100%;
  padding: 0.9rem 1rem;
  margin-top: 1.5rem;
  border-radius: 10px;
  border: 1px solid #cbd5e1;
  font-family: "Montserrat", sans-serif;
  min-height: 90px;
  resize: none;
}

.buttonGroup {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}

.buttonComentario {
  background-color: #1d4ed8;
  color: white;
  padding: 0.8rem 1.4rem;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.buttonComentario:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
}

/* === SCROLLBAR PERSONALIZADO === */
.container::-webkit-scrollbar {
  width: 8px;
}
.container::-webkit-scrollbar-thumb {
  background-color: #a0aec0;
  border-radius: 10px;
}
